<template>
<div class="gray">
  <div class="user-title-container" :style="{height: height + 'px'}">
    <div class="top">
      <div class="like" @click="handleFollow(user.id, 'user', user.isFollow)">

        <img v-if="user.isFollow" src="../assets/liked.svg" alt="">
        <img v-else src="../assets/like.svg" alt="">
        <p v-if="user.isFollow" class="followit">已关注</p>
        <p v-else>关注</p>
      </div>
    </div>
    <div class="user">
      <img :src="user.headimgurl" alt="">
      {{user.nickname}}
    </div>
    <div class="buttons">
      <div class="follow">
        关注: {{user.follow_number}}
      </div>
      <div class="follower">
        粉丝: {{user.fans_number}}
      </div>
    </div>
  </div>
    <div class="tabs">
      <div :class="['item', {'item-active': nav === 'looking'}]" @click="setNav('looking')">{{sex}}的动态</div>
      <div :class="['item', {'item-active': nav === 'activity'}]" @click="setNav('activity')">{{sex}}参与过</div>
      <div :class="['item', {'item-active': nav === 'comments'}]" @click="setNav('comments')">{{sex}}的评论</div>
    </div>
    
    <div class="tab">
      <div class="tab-container" v-if="nav === 'looking'" :style="{display: nav === 'looking' ? 'block' : 'none'}">
        <ul v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="10">
          <li v-for="(item, i) in looking" :key="i">
            <p class="message">参与了活动<span style="color: #369;">{{item.name}}</span></p>
            <card :item="item">
              </card>
          </li>
        </ul>
      </div>
      <div class="tab-container" v-else-if="nav === 'activity'" :style="{display: nav === 'activity' ? 'block' : 'none'}">
        <ul v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="10">
          <li v-for="(item, i) in looking" :key="i">
            <p class="message">参与了活动<span style="color: #369;">{{item.name}}</span></p>
            <card :item="item">
              </card>
          </li>
        </ul>
      </div>
      <div class="tab-container" v-else-if="nav === 'store'" :style="{display: nav === 'store' ? 'block' : 'none'}">
        <ul v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="10">
          <li v-for="(item, i) in list.store" :key="i">
            <store-following :item="item">
              </store-following>
          </li>
        </ul>
      </div>
    </div>

</div>
</template>
<script>
import { mapState } from 'vuex';
import moment from 'moment';
import Activity from '../components/Activity';
import api from '../api/user';
import api2 from '../api/activity';
import Card from '../components/card';

export default {
  components: { Card, Activity },
  mounted() {
    const rate = window.innerWidth / 16;
    this.height = rate * 9.5;
    this.handleData();
  },
  data() {
    return {
      user: {},
      sex: '',
      height: '',
      looking: [],
    };
  },
  computed: {
    ...mapState({
      nav: state => state.user.nav,
    }),
  },

  methods: {
    async handleData() {
      const id = this.$route.params.id;
      const res = await api.get(id);
      this.user = res.data;
      this.sex = res.data.sex === 1 ? '他' : '她';

      const res2 = await api2.getOtherList(id)(1);
      /**
       * id,
       * name,
       * location,
       * link
       */
      const looking = res2.data.map(v => ({
        ...v,
        name: v.title,
        id: v.id,
        location: v.address,
        link: 'Activity-detail',
        time: moment(v.start_time * 1000).format('YYYY-MM-DD'),
      }));
      this.looking = looking;
    },
    handleFollow(id, type, follow) {
      const cb = () => this.handleData();
      if (follow) {
        this.$store.dispatch('unfollow', { id, type, cb });
      } else {
        this.$store.dispatch('follow', { id, type, cb });
      }
    },
    setNav(nav) {
      this.$store.commit('setUserNav', nav);
    },
    loadMore() {},
  },
};
</script>
<style lang="scss" scoped>
.user-title-container {
  width: 100%;
  background: url('../assets/user-title.png');
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  .top {
    top: 0;
    width: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    img {
      width: 18px;
      margin-bottom: 2px;
    }
    .like {
      color: white;
      text-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
      margin: 10px 8px 0 0;
      p {
        display: inline;
        color: #ff5d57;
      }
      .followit {
        color: white;
      }
    }
  }
  .user {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    img {
      width: 25%;
      border-radius: 50%;
    }
    color: white;
    font-size: 22px;
    font-weight: bold;
  }
  .buttons {
    display: flex;
    padding: 5px 25%;
    justify-content: space-between;

    .follow,
    .follower {
      font-size: 13px;
      padding: 3px 10px;
      border: 2px solid white;
      border-radius: 20px;
      color: white;
    }
    .follower {
      background: #ff5d57;
      border-color: #ff5d57;
    }
  }
}
.tabs {
  width: 100%;
  display: flex;
  padding: 5px 45px;
  justify-content: space-between;
  background: white;

  .item {
    color: #999;
  }
  .item-active {
    color: #333;
    border-bottom: 2px solid #fdda06;
  }
}
.tab {
  width: 100%;

  .tab-container {
    display: block;
    width: 100%;
    background: #f2f2f2;
    padding: 15px;
    li {
      margin-bottom: 10px;
    }
    .message {
      margin: 0;
      font-size: 13px;
    }
  }
}
.gray {
  position: absolute;
  width: 100%;
  height: 100%;
  background: #f2f2f2;
}
</style>





